    callInvService(invoiceDto, listOfLrDto) {
        console.log(invoiceDto);
        console.log(listOfLrDto);
        this.showSpinnerActionForInvoice = true;
        this.stockService.addInvoiceDetails(invoiceDto).subscribe(
            (response) => {
                this.showSpinnerActionForInvoice = false;
                console.log(response);
                if (response.invoiceNumber != null) {
                    invoiceDto.invoiceNumber = response.invoiceNumber;
                    this.setHideLrInDummyInvoicePrint();
                    swal({
                        title: "Loading Sheet",
                        text: "Invoice No : " + response.invoiceNumber + " Created.Do You Want To Print This Invoice?",
                        icon: "info",
                        buttons: ["No", "Yes"]
                    }).then((isConfirm) => {
                        if (isConfirm) {
                            this.LoadingSheetBookingPrint(invoiceDto, listOfLrDto, response.invoiceNumber);
                        } else {
                            this.invoiceSummaryMethod(response.invoiceNumber);
                            // this.bookingInvoiceMethod(response.invoiceNumber);
                        }
                    });
                } else {
                    swal("Loading Sheet", "Problem Occurs While creating the Invoice..", "error");
                }
            }), (error) => {
                this.showSpinnerActionForInvoice = false;
                swal("Server Error", "Problem Occurs While getting Add Invoice Details", "error");
            },
            () => console.log('done');

    }

