    getNoEwayBillLrDetails() {
        this.ewayBillLrForLRDto = new LRDto();
        this.ewayLrs = "";
        this.countEwayLrs = 0;
        this.countEwayLrsAbove50K = 0;
        this.countEwayLrsBelow50K = 0;

        //this.ewayBillLrForLRDto.invoiceNumber ="NP/CLT/1920/30";
        this.ewayBillLrForLRDto.invoiceNumber = this.hireSlipDtoForCreateHireslipLocalStorage.invoiceNumber;
        this.ewayBillLrForLRDto.companyId = this.userDataDtoReturnSession.companyId;
        this.stockService.getLrsForNoEwayBillValidations(this.ewayBillLrForLRDto).subscribe(
            (response) => {
                console.log("response");
                console.log(response.length);
                console.log(response);
                if (response != null) {
                    this.returnEwayBillLrsLRDto = [];
                    this.returnEwayBillLrsLRDto.push(response);
                    this.hashmapEwayBill = new Map<String, Number>();
                    this.goodsValueFind = null;
                    for (let i = 0; i < this.returnEwayBillLrsLRDto.length; i++) {
                        if (this.hashmapEwayBill.get(this.returnEwayBillLrsLRDto[i].gstNoConsignee) == null) {
                            this.hashmapEwayBill.set(this.returnEwayBillLrsLRDto[i].gstNoConsignee,
                                this.returnEwayBillLrsLRDto[i].goodsValue);
                        } else {
                            this.goodsValueFind = this.hashmapEwayBill.get(this.returnEwayBillLrsLRDto[i].gstNoConsignee);
                            this.hashmapEwayBill.set(this.returnEwayBillLrsLRDto[i].gstNoConsignee,
                                this.returnEwayBillLrsLRDto[i].goodsValue + this.goodsValueFind);
                        }
                    }
                    this.goodsValueFind1 = null;
                    this.ewayBill = null;
                    for (let j = 0; j < this.returnEwayBillLrsLRDto.length; j++) {
                        // logic for eway status @Dated : 26/12/2018
                        this.goodsValueFind1 = this.hashmapEwayBill.get(this.returnEwayBillLrsLRDto[j].gstNoConsignee);
                        this.ewayBill = this.hashmapEwayBill.get(this.returnEwayBillLrsLRDto[j].eWayBillNo);
                        if (this.goodsValueFind1 >= 50000 && this.ewayBill == null) {
                            if (this.countEwayLrs == 0) {
                                this.ewayBill += this.returnEwayBillLrsLRDto[j].lrNumber;
                            } else {
                                this.ewayBill += ", " + this.returnEwayBillLrsLRDto[j].lrNumber;
                            }
                            this.countEwayLrs++;
                        }
                        // goods value greater than 50K and eway bill is not null
                        // goods value lesser than 50K and eway bill is not null
                        if (this.returnEwayBillLrsLRDto[j].goodsValue >= 50000 && this.ewayBill != null) {
                            this.countEwayLrsAbove50K = +this.countEwayLrsAbove50K + +1;
                        } else if (this.returnEwayBillLrsLRDto[j].goodsValue < 50000 && this.ewayBill != null) {
                            this.countEwayLrsAbove50K = +this.countEwayLrsAbove50K + +1;
                        }
                    }
                    $("#noOfNoEwbInConsolidatedSystem").val(this.countEwayLrs);
                }
            }), (error) => {
                swal("Error", "Server Error While Getting No EwayBill LR Details", "warning");
            },
            () => console.log('done');
    }

