    saveHireslipDetailsMethod() {
        console.log("CHECKING");
        this.hireSlipDtoSaveHireslip = new HireSlipDto();
        this.chkStatusHireSlipDto = new HireSlipDto();

        this.hireSlipDtoSaveHireslip = this.getUsersValue();
        console.log(this.hireSlipDtoSaveHireslip);

        this.chkStatusHireSlipDto.companyid = this.userDataDtoReturnSession.companyId;
        this.chkStatusHireSlipDto.hireslipnumber = this.hireslipNumber;
        this.showSpinnerActionForHireslip = true;
        this.stockService.chkHireslipStatus(this.chkStatusHireSlipDto).subscribe(
            (response) => {
                if (response) {
                    this.showSpinnerActionForHireslip = false;
                    console.log(response.status);
                    this.truckDataDtoValidateTruckType = new TruckDataDto();
                    this.truckDataDtoValidateTruckType = this.validateTruckTypeSelectionsOnGurWgt();
                    console.log(this.truckDataDtoValidateTruckType);
                    if (response.status != null && response.status == "Transit") {
                        swal("Warning", "Hireslip was already Departed", "warning");
                    } else if ($("#truckType").val() == null || $("#truckType").val() == ""
                        || $("#truckType").val() == 0) {
                        swal("Not Allowed", "Please select Truck Type to proceed!", "warning");
                    } else if (!this.truckDataDtoValidateTruckType.isValid) {
                        swal("Not Allowed", "The Truck Type selected does not match with the Total GUR Wt entered, kindly select the appropriate Truck Type.",
                            "warning");
                    } else {
                        /*  here addhireslipdetails service starting */
                        this.showSpinnerActionForHireslip = true;
                        this.stockService.addHireSlipDetails(this.hireSlipDtoSaveHireslip).subscribe(
                            (response) => {
                                this.showSpinnerActionForHireslip = false;
                                console.log(response);
                                if (response.status == "Failed") {
                                    swal("Failed", "Failed to save/update add hire slip details", "warning");

                                } else if (response.status == "Success") {
                                    this.printInvoiceAlone(this.hireSlipDtoSaveHireslip, response.hireslipnumber);
                                    if (this.hireSlipDtoForCreateHireslipLocalStorage.fromstation == "KVH" &&
                                        (!($("#destination").val() == "Bangalore"))) {
                                        if (!$("#isMultiInvoiceTripId").is(':checked')) {
                                            //ftTrackingFlow();
                                        } else if ($("#isMultiInvoiceTripId").is(':checked') &&
                                            $("#islastPointDeliveryId").is(':checked')) {
                                            //ftTrackingFlow();
                                        }
                                    } else {
                                        if (!$("#isMultiInvoiceTripId").is(':checked')) {
                                            //ftTrackingFlow();
                                        } else if ($("#isMultiInvoiceTripId").is(':checked') &&
                                            $("#islastPointDeliveryId").is(':checked')) {
                                            //ftTrackingFlow();
                                        }
                                    }
                                    console.log("SAVE");
                                    //this.stepper.reset();
                                    //this.overAllResetComplete();
                                }
                            }), (error) => {
                                this.showSpinnerActionForHireslip = false;
                                swal("Error", "Server Error While Save Hireslip Details", "warning");
                            }, () => console.log('done');
                    }
                    /*  here addhireslipdetails service ending */
                }
            }), (error) => {
                this.showSpinnerActionForHireslip = false;
                swal("Error", "Server Error While Getting checking Hireslip Status", "warning");
            }, () => console.log('done');
    }

    validateTruckTypeSelectionsOnGurWgt() {
        //Pending work of comment line erroer shwg
        this.isValid = false;
        this.truckDto = new TruckDataDto();
        this.truckTypeId = null;
        //this.truckTypeId = this.modelTruckType.id;
        this.truckTypeId = this.selectedTruckTypeId;
        this.truckDto = this.hmTruckTypeDto.get(this.truckTypeId);
        this.validateTruckTypeTotGurWgt = $("#gurWeightTotal").val();
        if (this.truckDto != null) {
            if (this.validateTruckTypeTotGurWgt < this.truckDto.fromRange ||
                this.totalGurWgt > this.truckDto.toRange) {
                this.isValid = false;
            } else {
                this.isValid = true;
            }
            this.truckDto.isValid = this.isValid;
        }
        return this.truckDto;
    }


