    uploadFileOfTruckOwnerDetails() {
        this.isUploadedTruckOwner = false;
        if ($("#truckOwnerFileUpload").val() != null && $("#truckOwnerFileUpload").val() != "") {
            if ((this.selectedFileTruckOwner.type != null &&
                (this.selectedFileTruckOwner.type == "image/jpeg" ||
                    this.selectedFileTruckOwner.type == "image/jpg" ||
                    this.selectedFileTruckOwner.type == "image/png"))) {
                var splitTypeTruckOwner = this.selectedFileTruckOwner.type.split("/");
                var fileNameDecl = $("#panNumber").val() + "_" + $("#lorryNumber").val();
                const commonListTruckOwner = "vehicledriverdocs && truckownerdeclaration && ASRAR && " + splitTypeTruckOwner[1] + "&&" + fileNameDecl + "";
                const formData = new FormData();
                formData.append(commonListTruckOwner, commonListTruckOwner);
                formData.append('myfileTruckOwner', this.selectedFileTruckOwner, this.selectedFileTruckOwner.name);
                this.masterReadService.saveRCInsurancePanForTruckMasterFileUpload(formData).subscribe(
                    (response) => {
                        if (response) {
                            //console.log(response[0]);
                            if (response[0] != "Error") {
                                this.isUploadedTruckOwner = true;
                                //String[] declFileParts = fileNameDecl.split("_");
                                var declFileParts = fileNameDecl.split("_");
                                this.truckDataDtoTruckOwner = new TruckDataDto();
                                this.truckDataDtoTruckOwner.panNo = declFileParts[0];
                                this.truckDataDtoTruckOwner.truckNumber = declFileParts[1];
                                this.truckDataDtoTruckOwner.mode = "particularTruck";
                                this.truckDataDtoTruckOwner.companyId = this.userDataDtoReturnSession.companyId;
                                this.truckDataDtoTruckOwner.financialYear = this.userDataDtoReturnSession.financialYearSplit;

                                if (this.isUploadedTruckOwner) {
                                    if ($("#truckOwnerFileUpload").val() != null && $("#truckOwnerFileUpload").val() != "") {
                                        const constImgUrlTruckOwner = "http://vehicledriverdocs.s3.amazonaws.com/truckownerdeclaration/" + fileNameDecl + "_" + $("#truckOwnerFileUpload").val() + "";
                                        this.truckDataDtoTruckOwner.imgURLPan = constImgUrlTruckOwner;
                                    }
                                    /* here starting masterReadService service */
                                    this.masterReadService.setTruckOwnerDetails(this.truckDataDtoTruckOwner).subscribe(
                                        (response) => {
                                            if (response) {
                                                //console.log(this.modeMain);
                                                if (this.modeMain != null && this.modeMain == "Save") {
                                                    this.modalRefferenceTruckOwnerPopUp.close();
                                                    //pending method here need to call
                                                    this.saveHireslipDetailsMethod();
                                                } else if (this.modeMain != null && this.modeMain == "Depart") {
                                                    this.modalRefferenceTruckOwnerPopUp.close();
                                                    this.setDepartureMtd();
                                                }
                                            }
                                        }), (error) => swal("ERROR", "Server Error While setTruckOwnerDetails", "error"),
                                        () => console.log('done');
                                    /* here ending a masterReadService  service */
                                }
                            } else {
                                swal("ERROR", "Correct the Truck Owner File and Upload Again", "error");
                            }
                        }
                    }), (error) => {
                        swal("Error", "Server Error While Uploading a Truck Owner File", "warning")
                    }, () => console.log('done');
            }
        } else {
            swal("Mandatory Field", "Please Browse the Image To Upload", "warning");
        }
    }
