    ftTrackingFlow() {
        this.hireSlipDtoForFtTrackingUserValue = new HireSlipDto();
        this.hireSlipDtoForFtTrackingUserValue = this.getUsersValue();
        if (this.isFTTripReq &&
            this.hireSlipDtoForFtTrackingUserValue.trackingFTTripId == null) {
            this.showSpinnerActionForHireslip = true;
            this.stockService.trackingIntgTripFT(this.hireSlipDtoForFtTrackingUserValue).subscribe(
                (response) => {
                    this.showSpinnerActionForHireslip = false;
                    console.log(response);
                    if (response.trackingFTStatus) {
                        swal("Success", "Tracking integration initiated successfully, to complete the Consent process," +
                            response.trackingCarrierMsg + ".", "success");
                    } else {
                        swal("Failed", "Tracking Integration failed, please try again or contact Admin", "warning");
                    }
                }),
                (error) => {
                    this.showSpinnerActionForHireslip = false;
                    swal("Error", "Server Error While Tracking Integration TripFT,please try again or contact Admin", "warning");
                }, () => console.log('done');
        } else if (this.isFTTripReq &&
            this.hireSlipDtoForFtTrackingUserValue.trackingFTTripId > 0 &&
            this.driverNumberChangeTemp != null &&
            !this.hireSlipDtoForFtTrackingUserValue.driverMobileNumber == this.driverNumberChangeTemp) {

            this.showSpinnerActionForHireslip = true;
            this.stockService.trackingIntgTripFTUpdate(this.hireSlipDtoForFtTrackingUserValue).subscribe(
                (response) => {
                    this.showSpinnerActionForHireslip = false;
                    console.log(response);
                    if (response.trackingFTStatus) {
                        swal("Success", "Driver number updated successfully and integrated with the Trip, to complete the Consent process," +
                            response.trackingCarrierMsg + ".", "success");
                    } else {
                        swal("Failed", "Failed to update the Driver number with Tracking, please try again or contact Admin", "warning");
                    }
                }),
                (error) => {
                    this.showSpinnerActionForHireslip = false;
                    swal("Error", "Failed to update the Driver number with Tracking, please try again or contact Admin", "warning");
                }, () => console.log('done');
        }
    }

